type BudgetStatus = 'critical' | 'warning' | 'normal';

interface BudgetInfo {
  used: number;
  total: number;
  percentage: string;
  remaining: number;
  status: BudgetStatus;
}

interface DetentionConfig {
  tokenBudget: number;
  fallbackBudget: number;
  compressionQuality: number;
  cacheExpiry: number;
  healthCheckInterval: number;
  healthCheckTimeout: number;
}

interface DetentionErrorInfo {
  message: string;
  context: string;
  timestamp: number;
  stack?: string;
}

interface DetentionState {
  mode: 'detecting' | 'external' | 'fallback';
  tokenUsed: number;
  lastHealthCheck: number;
  errors: DetentionErrorInfo[];
}

interface CacheEntry<T> {
  value: T;
  expiry: number | null;
}

interface CacheManager {
  set<T>(key: string, value: T, ttl?: number | null): void;
  get<T>(key: string): T | null;
  has(key: string): boolean;
  delete(key: string): void;
  clear(): void;
  cleanup(): void;
}

interface EnvironmentInfo {
  isSillyTavern: boolean;
  version: string | null;
  hasHelper: boolean;
  helperVersion: string | null;
  hasDataTable: boolean;
}

class EventEmitter {
  private readonly events = new Map<string, Array<(data?: unknown) => void>>();

  on(event: string, callback: (data?: unknown) => void) {
    if (!this.events.has(event)) {
      this.events.set(event, []);
    }
    this.events.get(event)!.push(callback);
  }

  off(event: string, callback: (data?: unknown) => void) {
    const callbacks = this.events.get(event);
    if (!callbacks) return;
    const index = callbacks.indexOf(callback);
    if (index > -1) callbacks.splice(index, 1);
  }

  emit(event: string, data?: unknown) {
    const callbacks = this.events.get(event);
    if (!callbacks) return;
    callbacks.forEach(cb => {
      try {
        cb(data);
      } catch (error) {
        console.error(`[]  ${event} :`, error);
      }
    });
  }

  once(event: string, callback: (data?: unknown) => void) {
    const wrapper = (data?: unknown) => {
      callback(data);
      this.off(event, wrapper);
    };
    this.on(event, wrapper);
  }
}

