                  const after = numStr.slice(shiIndex + 1);
                  const beforeNum = before in chineseDigits ? chineseDigits[before] : 0;
                  const afterNum = after in chineseDigits ? chineseDigits[after] : 0;
                  result = (beforeNum || 1) * 10 + afterNum; // ""1""
